export default {
	data() {
		return {
			payType: 1,
			price: 0,
			orderNo: '',
			submiting: false
		};
	},
	computed: {

	},
	onLoad(options) {
		this.price = options.price
		this.orderNo = options.orderno
	},

	methods: {
		//选择支付方式
		changePayType(type) {
			this.payType = type;
		},
		//确认支付
		confirm: async function() {
			const that = this
			if (that.payType === 1) {
				that.submiting = true
				that.$api.request('order', 'wxPrepay', {
					orderNo : that.orderNo
				}, failres => {
					that.submiting = false
					that.$api.msg(failres.errmsg)
				}).then(prepayRes => {
					that.submiting = false

					const payParam = {
						appId: prepayRes.data.appId,
						nonceStr: prepayRes.data.nonceStr,
						package: prepayRes.data.packageValue,
						timeStamp: prepayRes.data.timeStamp,
						signType: prepayRes.data.signType,
						paySign: prepayRes.data.paySign,
					}














					uni.requestPayment({
						provider: 'wxpay',

						...payParam,




						success: function(res) {
							uni.redirectTo({
								url: '/pages/pay/success'
							})
						},
						fail: function(res) {
							console.log("支付过程失败");
							that.$api.msg(JSON.stringify(res))
						},
						complete: function(res) {
							console.log("支付过程结束")
						}
					});























				})
				
			} else if (that.payType === 3) {
				that.submiting = true
				that.$api.request('order', 'offlinePrepay', {
					orderNo : that.orderNo
				}, failres => {
					that.submiting = false
					that.$api.msg(failres.errmsg)
				}).then(res => {
					uni.redirectTo({
						url: '/pages/pay/success'
					})
				})
			}
			
		}
	}
}
